import React from "react";
import type { Metadata } from "next";
import { Geist } from "next/font/google";
import "./globals.css";

const geistSans = Geist({
    variable: "--font-geist-sans",
    subsets: ["latin"],
});

export const metadata: Metadata = {
    title: "Pickom - People-Powered Delivery",
    description: "Send packages with travelers going your way",
    viewport: {
        width: "device-width",
        initialScale: 1,
        maximumScale: 1,
        userScalable: false,
        viewportFit: "cover"
    },
    themeColor: "#000000",
    appleWebApp: {
        capable: true,
        statusBarStyle: "black-translucent",
        title: "Pickom"
    },
    formatDetection: {
        telephone: false,
    },
};

export default function RootLayout({
    children,
}: Readonly<{
    children: React.ReactNode;
}>) {
    return (
        <html lang="en">
            <head>
                <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no, viewport-fit=cover" />
                <meta name="theme-color" content="#000000" />
                <meta name="apple-mobile-web-app-capable" content="yes" />
                <meta name="apple-mobile-web-app-status-bar-style" content="black-translucent" />
                <meta name="apple-mobile-web-app-title" content="Pickom" />
                <meta name="mobile-web-app-capable" content="yes" />
                <meta name="format-detection" content="telephone=no" />
            </head>
            <body className={`${geistSans.variable} antialiased bg-gray-900 text-white min-h-screen overflow-x-hidden`}>
                {/* Mobile Phone Container */}
                <div className="w-full min-h-screen bg-gray-900 flex justify-center items-center p-4">
                    <div className="w-full max-w-sm bg-black min-h-screen relative rounded-3xl shadow-2xl border border-gray-800 overflow-hidden">
                        {children}
                    </div>
                </div>
            </body>
        </html>
    );
} 